// Vue.use(ElementUI);


import tableTree from 'vue-table-with-tree-grid';
// 注册组件(以后用组件时候的标签的名字)
Vue.component("table-tree",tableTree);


// // 配置vue-quill-editor
// require styles
import VueQuillEditor from "vue-quill-editor"
