// axios
// 配置axios
import Axios from "axios";
Vue.prototype.$http = Axios;
Axios.defaults.baseURL = "http://www.itsse.cn:8888/api/private/v1/";
// Axios.defaults.baseURL = "http://127.0.0.1:7777";
Axios.interceptors.request.use(function(config){
  config.headers.Authorization = window.sessionStorage.getItem('token');
  return config
},function(error){
  return Promise.reject(error)
})
Vue.config.productionTip = false;

Vue.filter('forDate',function(val){
  var time = new Date(val);
  var year = time.getFullYear();
  var month = time.getMonth() + 1;
  var day = time.getDate();
  var hour = time.getHours();
  var minute = time.getMinutes();
  var second = time.getSeconds();
  month = month < 10 ? '0' + month : month;
  day = day < 10 ? '0' + day : day;
  hour = hour < 10 ? '0' + hour : hour;
  minute = minute < 10 ? '0' + minute : minute;
  second = second < 10 ? '0' + second : second;
  time = year + '年' + month + '月' + day + '日  ' + hour + '时' + minute + '分' + second + '秒';
  return time;
})

Vue.prototype.diaClosed = function (ref){
  this.$refs[ref].resetFields();
}

new Vue({
  router,
  render: h => h(App)
}).$mount("#app")