import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);
const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      redirect: '/home'
    },
    {
      path: "/home",
      name: "home",
      component: Home,
      children: [{
          path: '/home',
          redirect: "/welcome"
        },
        {
          path: '/welcome',
          component: () => import('@/components/Welcome.vue')
        },
        {
          path: '/users',
          component: () => import("@/components/UserList.vue")
        },
        {
          path: '/roles',
          component: () => import("@/components/RolesList.vue")
        },
        {
          path:"/rights",
          component: () => import("@/components/RightsList.vue")
        },
        {
          path:'/goods',
          component: () => import("@/components/GoodsList.vue")
        },
        {
          path:'/goodsAdd',
          component: () => import("@/components/GoodsAdd.vue")
        },{
          path:'/params',
          component: () => import("@/components/ParamsList.vue")
        },
        {
          path:"/categories",
          component:()=>import("@/components/CategoriesList.vue")
        },
        {
          path:"/orders",
          component:()=>import("@/components/OrdersList.vue")
        },
        {
          path:"/reports",
          component:()=>import("@/components/ReportsList.vue")
        },
        {
          path:"/kaspersky",
          component:()=>import("@/components/KasperskyList.vue")
        }
      ]
    },
    {
      path: "/login",
      name: "login",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import( /* webpackChunkName: "about" */ "./views/login.vue")
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.path == "/login") return next()
  const token = window.sessionStorage.getItem("token")
  if (!token) {
    window.sessionStorage.removeItem('token')
    return next("/login")
  }
  next()
});
export default router;
