<template>
  <div class="home">
    <el-container class="home-top">
      <el-header>
        <img src="../assets/logo.png" alt />
        <h2>电商管理平台</h2>
        <el-button type="info" @click="logoOut">退出</el-button>
      </el-header>
      <el-container class="cont">
        <el-aside :width="collapse?'64px':'200px'">
          <div class="toggle-aside" @click="collapse=!collapse">|||</div>
          <el-menu
            class="el-menu-vertical-demo"
            background-color="#373d41"
            text-color="#fff"
            active-text-color="#ffd04b"
            :unique-opened="true"
            :router="true"
            :collapse="collapse"
          >
            <el-submenu :index="item.path" v-for="(item,index) in  menusList" :key="item.id">
              <template slot="title">
                <i :class="iconList[index]"></i>
                <span>{{item.authName}}</span>
              </template>
                <el-menu-item :index="subItem.path" v-for="(subItem,subIndex) in item.children" :key="subItem.id" :z="subIndex">
                  <i class="el-icon-menu"></i>
                  <span>{{subItem.authName}}</span>
                </el-menu-item>
            </el-submenu>
          </el-menu>
        </el-aside>
        <el-main><router-view></router-view></el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      collapse:false,
      menusList:[],
       iconList: [
        "el-icon-user-solid",
        "el-icon-s-promotion",
        "el-icon-s-goods",
        "el-icon-s-order",
        "el-icon-s-marketing",
        "el-icon-loading"
      ],
    };
  },
  methods: {
    // 退出
    logoOut() {
      window.sessionStorage.removeItem("token");
      this.$router.push("/login");
    },
    // 获取权限列表
    async getMenuList(){
      let {data:res} = await this.$http.get("menus");
      if(res.meta.status !=200){
        return this.$message.error('获取权限列表失败')
      }
      console.log(res);
      
      let arrar = {
        authName:"拓展应用",
        children:[{
          authName:"全球实时图",
          children:{},
          id:201,
          order:null,
          path:'kaspersky'
        }],
        id:200,
        order:77,
        path:'extend',
      }
      res.data.push(arrar)
      console.log(res);
      
      this.menusList = res.data;
      this.$message.success('靓仔你来了')
      
    }
  },
  created:function(){
    this.getMenuList()
  }
};
</script>

<style lang="less" scoped>
.el-header {
  background: #373d41;
  line-height: 60px;
  img {
    width: 50px;
    height: 50px;
    float: left;
  }
  h2 {
    padding: 0;
    margin: 0;
    float: left;
    color: #fff;
    font-weight: 400;
  }
  button {
    float: right;
    margin-top: 10px;
  }
}
.el-aside {
  background: #373d41;
}
.el-menu {
  border: 0px;
  overflow: hidden;
}
.toggle-aside {
   color: #fff;
    background-color: #4a5064;
    text-align: center;
    height: 22px;
    line-height: 20px;
    font-size: 12px;
    letter-spacing: 1px;
    // 让用户不能选中某一个东西
    user-select: none;
    cursor: pointer;
}
.home {
  height: 100%;
}
.home .el-main {
  height: 100%;
}
.home .cont {
  height: 100%;
}
.home .home-top {
  height: 100%;
}
</style>

